const rootStore = {
  namespaced: true,
  state: {
    rt_webSk:null, // 루트 웹소켓 인스턴스
    rt_sk_list:[],   // 활성 소켓 리스트
    rt_in_list:[],  // in 정보
    rt_out_list:[],  // out 정보
    rt_body_list:[],
    rt_body_dt_list:[],
    rt_msg_list:[],
    rt_hd_list:[],
    rt_hd_dt_list:[],
    rt_inmemory_list:[],
    rt_proc_list:[],
    rt_pos_list:[],
    rt_line_list:[],
    rt_bz_list:[],
    rt_sch_list:[]
  },
  getters: {
    getInfoTitle_: (state) => {
      return state.title
    },
    getInfoContents_: (state) => {
      return state.content
    },
    getRtMsgList_:(state)=>{
        let newMsg = state.rt_msg_list.pop(0)
        return newMsg
    }

  },
  mutations: {
    dufaltAssign(state, payload){  //{key:"", val:""} 형식
      state[payload.key]  = payload.val
    },
    appendRtMsgList(state, payload){   // 실시간 메시지 데이터 push
      state.rt_msg_list.push(payload)
    }
  },
  actions: {
    // getMsgBodyInfo: ({ commit }, msgId) => {

    //   for(let i in state.rt_body_list){
    //     if(state.rt_body_list[i].MSG_ID == msgId){
    //       console.log(state.rt_body_list[i])
    //     }
    //   }
    //   return 
    // },
  }
}

export default rootStore