/* eslint-disable no-unused-vars */
import { webAPI } from '@/api/webApi.js'

/* api url  */
const getComboList = '/getComboList.json'


const comboStore = {
  namespaced: true,
  state: {
    comboList: [],

  },
  getters: {
    get_comboList: (state) => {
      return state.comboList
    },

  },
  mutations: {
    set_comboList: (state, payload) => {
      state.comboList = payload
    },



  },
  actions: {

    ac_pkSearch_: ({ commit }, params) => {
      webAPI.post(getComboList, params)
        .then(res => {
          console.log(res.data.result)
          commit('set_comboList', res.data.result)
        })
        .catch(err => {
          console.log(err)
        })
    }

  }
}

export default comboStore