<template>
<section >

  <div class="mycore-table-header">
    <div class="left-header">
       <el-select v-model="perCnt" clearable size="mini" placeholder="Select">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
       <!-- <el-checkbox v-model="autoRefrash">Auto refresh</el-checkbox> -->
    </div>
    <div class="right-header">
        <!-- <el-button icon="el-icon-refresh" circle></el-button> -->
    </div>
  </div>
 <el-table
    :size="'mini'"
    :class="'mycore-table'"
    :data="getPagedData(currentPage,rt_line_list,perCnt)"
    :header-row-class-name="'table_hd'"
    border
    style="width: 100%">

    <el-table-column
      prop="LINE_CD"
      label="LINE_CD">
    </el-table-column>

    <el-table-column
      prop="LINE_NM"
      label="LINE_NM">
    </el-table-column>

    <el-table-column
      prop="LINE_SPD"
      label="LINE_SPD"
      width="180">
    </el-table-column>

    <el-table-column
      prop="MAPP_CD"
      label="MAPP_CD">
    </el-table-column>

    <el-table-column
      prop="SORT_NO"
      label="SORT_NO">
    </el-table-column>

    <el-table-column
      prop="ACTIVE_YN"
      label="ACTIVE_YN">
    </el-table-column>

    <el-table-column
      prop="REG_DT"
      label="REG_DT">
    </el-table-column>

    <el-table-column
      prop="UPD_DT"
      label="UPD_DT">
    </el-table-column>
  </el-table>
  <el-pagination
  small
  layout="prev, pager, next"
  @current-change="changePage"
  :page-size="perCnt"
  :total="totalSize">
</el-pagination>

</section>
</template>

<script>

import {targetComputed, targetMethod} from '../store/inmemoryHelper'
import {rootComputed, rootMethod} from '../../../store/rootStore/rootHelper'

export default {
    props:{
        
    },
    data() {
      return {
        perCnt:10,
        currentPage:1,
        totalSize:0,
        autoRefrash:false,
        polling:null,
        options: [{
          value: 10,
          label: '10'
        }, {
          value: 20,
          label: '20'
        }, {
          value: 30,
          label: '30'
        }, {
          value: 60,
          label: '60'
        }]
      }
    },

    watch:{
      autoRefrash(nval){
        if(nval){
            this.callFunction()
        }else{
          clearInterval(this.polling)
        }
      }
    },

    computed:{
      ...rootComputed,
        ...targetComputed
      
      
    },
    created(){
      //this.getPagedData(1)
      //console.log(this.rt_proc_list)
    },

    methods: {
        ...rootMethod,
        ...targetMethod,

        getPagedData(currentPage,list,perPage){
          const startIndex = (currentPage - 1) * (perPage);
          const endIndex = startIndex + (perPage);
          
          let temp = []
          for(let i=0; i<list.length ; i++){
            if(i >= startIndex && i < endIndex){
              temp.push(list[i])
            }
          }
          this.totalSize = list.length
          return temp
        },

        changePage(index){
          this.currentPage = index
        },
        callFunction () {

            if(this.polling !== null ){ clearInterval(this.polling) }

            this.polling = setInterval(()=>{
              let sendForm = {
                COMMAND:'SYSTEM_MSG',
                FLAG: 'REFRASH_DATA',  // IN_SEND_TEST, OUT_SEND_TEST
                DATA_LIST:'LINE_LIST'
              }
              this.rt_webSk.send(JSON.stringify(sendForm))
            },2000)

        },
        
      },
      beforeDestroy(){
		    clearInterval(this.polling)
	  }
  }

</script>

<style>
.custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 8px;
  }

.inner-title{

}

.mycore-table-header{
  display: flex;
  justify-content: space-between;
}
.header-item{
  display: flex;
}
</style>