/* eslint-disable no-unused-vars */
import { webAPI } from '@/api/webApi.js'

/* api url  */
const ac_rest_post = '/commQuery.json'
const ac_commQuerySelect = '/commQuerySelect.json'
const ac_excelToTTs = '/excelToTTs.json'

const inmemoryStore = {
  namespaced: true,
  state: {
    st_resultStat: false,
    st_in_list:[],
    st_out_list:[],
    st_sk_list:[]
  },
  getters: {
    get_defaultAssign(state, payload){
      return state[payload]
    }
  },
  mutations: {
    
    dufaltAssign(state, payload){
      state[payload.key]  = payload.value
    }
  },
  actions: {
    ac_rest_post: ({ commit }, params) => {
      return webAPI.post(ac_rest_post, params)
        .then(res => {
          return res
        })
        .catch(err => {
          console.log(err)
        })
    },

    ac_commQuerySelect: ({ commit }, params)=>{
      return webAPI.post(ac_commQuerySelect, params)
      .then(res => {
        return res
      })
      .catch(err => {
        return err
      })
    },

    ac_excelToTTs: ({ commit }, params) => {
      return webAPI.post(ac_excelToTTs, params)
        .then(res => {
          return res
        })
        .catch(err => {
          return err
        })
    },
  }
}

export default inmemoryStore