<template>
<section class="sch-bz-area">

<div style="width:50%">
 <el-table
    :size="'mini'"
    :class="'mycore-table'"
    :data="rt_bz_list"
    :header-row-class-name="'table_hd'"
    :empty-text="'NO DATA'"
    border
    style="width: 100%">
<el-table-column
      prop="SK_GROUP"
      label="SK_GROUP"
      width="200">
    </el-table-column>
    <el-table-column
      prop="BZ_TYPE"
      label="BZ_TYPE"
      width="120">
    </el-table-column>

    <el-table-column
      prop="BZ_METHOD"
      label="BZ_METHOD"
      >
    </el-table-column>
    <el-table-column
      prop="BZ_DESC"
      label="BZ_DESC"
      >
    </el-table-column>
  </el-table>
</div>

<div style="width:50%">
<el-table
    :size="'mini'"
    :class="'mycore-table'"
    :data="rt_sch_list"
    :header-row-class-name="'table_hd'"
    :empty-text="'NO DATA'"
    border
    style="width: 100%">
<el-table-column
      prop="SCH_ID"
      label="SCH_ID"
      width="200">
    </el-table-column>
    <el-table-column
      prop="SCH_JOB"
      label="SCH_JOB"
      width="120">
    </el-table-column>

    <el-table-column
      prop="BZ_METHOD"
      label="BZ_METHOD"
      >
    </el-table-column>
    
  </el-table>
</div>
</section>
</template>

<script>

import {targetComputed, targetMethod} from '../store/testApiHelper'
import {rootComputed, rootMethod} from '../../../store/rootStore/rootHelper'

export default {
    props:{
        
    },
    data() {
      return {
        perCnt:10,
        currentPage:1,
        totalSize:0,
        autoRefrash:false,
        polling:null,
        options: [{
          value: 10,
          label: '10'
        }, {
          value: 20,
          label: '20'
        }, {
          value: 30,
          label: '30'
        }, {
          value: 60,
          label: '60'
        }]
      }
    },

    watch:{
      autoRefrash(nval){
        if(nval){
            this.callFunction()
        }else{
          clearInterval(this.polling)
        }
      }
    },

    computed:{
      ...rootComputed,
        ...targetComputed
      
      
    },
    created(){
      //this.getPagedData(1)
      //console.log(this.rt_proc_list)
    },

    methods: {
        ...rootMethod,
        ...targetMethod,

        getPagedData(currentPage,list,perPage){
          const startIndex = (currentPage - 1) * (perPage);
          const endIndex = startIndex + (perPage);
          
          let temp = []
          for(let i=0; i<list.length ; i++){
            if(i >= startIndex && i < endIndex){
              temp.push(list[i])
            }
          }
          this.totalSize = list.length
          return temp
        },

        changePage(index){
          this.currentPage = index
        },
        callFunction () {

            if(this.polling !== null ){ clearInterval(this.polling) }

            this.polling = setInterval(()=>{
              let sendForm = {
                COMMAND:'SYSTEM_MSG',
                FLAG: 'REFRASH_DATA',  // IN_SEND_TEST, OUT_SEND_TEST
                DATA_LIST:'POS_LIST'
              }
              this.rt_webSk.send(JSON.stringify(sendForm))
            },2000)

        },
        
      },
      beforeDestroy(){
		    clearInterval(this.polling)
	  }
  }

</script>

<style>
.custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 8px;
}

.mycore-table-header{
  display: flex;
  justify-content: space-between;
}
.header-item{
  display: flex;
}

.sch-bz-area{
  display: flex;
}
</style>