

const jsUtil = {
    ConvCamelNaming : (str) => {
        var tmp = str.split('_');
        var rtn = "";
        for(var i = 0; i < tmp.length; i ++) {
            var str1 = tmp[i].toLowerCase();
            if(i > 0 && str1.length > 0) { str1 = str1.substr(0,1).toUpperCase() + str1.substr(1, str1.length - 1); }
            rtn += str1;
        }
        return rtn;

    },
    fnLPad : (str,length,sep) => {
        var strResult = '';
		var sbAddChar = ''

        console.log(str.length)
        console.log(length)

        for(var i= str.length ; i < length ; i++){
            sbAddChar = sbAddChar+sep
            console.log(i)
        }
		strResult = sbAddChar + str
		return strResult
    },

    fnGetComboData : (str) => {
        return str;
    }
}
export default {
    install(Vue){
        Vue.prototype.$ConvCamelNaming = jsUtil.ConvCamelNaming;
        Vue.prototype.$GetComboData = jsUtil.fnGetComboData
        Vue.prototype.$fnLPad = jsUtil.fnLPad
    }
}

