import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
import rootStore from '@/store/rootStore/rootStore'

export const rootComputed ={
    ...mapState('rootStore',Object.keys(rootStore.state)),
    ...mapGetters('rootStore',Object.keys(rootStore.getters))
}

export const rootMethod ={
    ...mapActions('rootStore',Object.keys(rootStore.actions)),
    ...mapMutations('rootStore',Object.keys(rootStore.mutations))
}