import { webInterceptor } from './interceptor.js'
/* import axios from 'axios' 인터셉터를 사용하므로 나중에 사용 */

const defualtHeader = { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
const multipartHeader = {'Content-Type': 'multipart/form-data', 'Access-Control-Allow-Origin': '*'}

/* 모든 파라미터는 자바스크립트 FormData 객체 이다
   리턴은 promise 이고 각각 store에서 반화값을 핸들링하면 된다. */
export const webAPI = {
  /* post 요청 */
  post (url, ObjectPram) {
    return webInterceptor.post(url, ObjectPram,
      {
        headers: defualtHeader,
        timeout: 2000
      })
  },
  postNoParam (url, ObjectPram) {
    return webInterceptor.post(url,
      {
        jsonList: ObjectPram,
        headers: defualtHeader,
        timeout: 2000
      })
  },
  get (url) {
    return webInterceptor.get(url)
  },
  /* get 요청 */
  get2 (url, param) {
    return webInterceptor.get(url,
      {
        params: param,
        headers: defualtHeader,
        timeout: 2000
      })
  },
  /* 파일포함 post 요청 */
  filePost (url, formData) {
    return webInterceptor.post(url, formData,
      {
        headers: multipartHeader,
        timeout: 2000
      })
  }
}