/* eslint-disable no-unused-vars */
import { webAPI } from '@/api/webApi.js'

/* api url  */
const getDBList = '/getDBList.json'
const searchDBList = '/searchDBList.json'
const getDBInfo = '/getDbInfo.json'
const getDbColum = '/getDbColum.json'
const getRelationPk = '/getRelationPk.json'
const excelDownload ='/excel/download/tables.json'
const searchPK = '/searchPK.json'

const columnAndPk = '/columnAndPk.json'


const dbStore = {
  namespaced: true,
  state: {
    DBList: [],
    DBsearch: [],
    DBInfo: {},
    DBColum:[],
    relationPk: [],
    searchPKList: []
  },
  getters: {
    getDbList_: (state) => {
      return state.DBList
    },
    getDbSearchList_: (state) => {
      return state.DBsearch
    },
    get_DbInfo_: (state) => {
      return state.DBInfo
    },
    get_DbColum_: (state) => {
      return state.DBColum
    },
    get_relationPK_: (state) => {
      return state.relationPk
    },
    get_searchPKList: (state) => {
      return state.searchPKList
    }

  },
  mutations: {
    setDbList_: (state, payload) => {
      state.DBList = payload
    },

    setDbSearchData_: (state, payload) => {
      state.DBsearch = payload
    },
    set_DbInfo_ : (state, payload) => {
      state.DBInfo = payload
    },
    set_DbColum_ : (state, payload) => {
      state.DBColum = payload
    },
    set_relationPK_: (state, payload) => {
      state.relationPk = payload
    },
    set_searchPKList: (state, payload) => {
      state.searchPKList = payload
    }

  },
  actions: {
    acDbList_: ({ commit }) => {
      webAPI.post(getDBList, {})
        .then(res => {

          commit('setDbList_', res.data.result)
        })
        .catch(err => {
          console.log(err)
        })
    },
    acDbSearchList_: ({ commit }, param) => {
      webAPI.post(searchDBList, param)
        .then(res => {

          commit('setDbSearchData_', res.data.result)
        })
        .catch(err => {
          console.log(err)
        })
    },
    ac_DbInfo_: ({ commit }) => {
      webAPI.post(getDBInfo,{})
        .then(res => {

          commit('set_DbInfo_', res.data)

          return res
        })
        .catch(err => {
          console.log(err)
        })
    },
    ac_DbColum_: ({ commit }, params) => {
      webAPI.post(getDbColum,params)
        .then(res => {

          commit('set_DbColum_', res.data.result)
        })
        .catch(err => {
          console.log(err)
        })
    },
    ac_relationPK_: ({ commit }, params) => {
      webAPI.post(getRelationPk,params)
        .then(res => {
        
          commit('set_relationPK_', res.data.result)
        })
        .catch(err => {
          console.log(err)
        })
    },
    ac_excelDownload_: ({ commit }) => {
      webAPI.post(excelDownload,{})
        .then(res => {
          console.log(res)
        })
        .catch(err => {
          console.log(err)
        })
    },
    ac_pkSearch_: ({ commit }, params) => {
      webAPI.post(searchPK,params)
        .then(res => {
          console.log(res.data.result)
          commit('set_searchPKList', res.data.result)
        })
        .catch(err => {
          console.log(err)
        })
    },
    ac_columnAndPk_: ({ commit }, params) => {
      return webAPI.post(columnAndPk,params)

    }
  }
}

export default dbStore