<template>
<div style="height:100%">

<h5 class="title_h"></h5>
<section>
  <el-button size="mini" @click="refrashInitData('INIT_DATA')"><span class="reload-btn">기준정보 Reload</span></el-button>
  <el-button size="mini" @click="refrashInitData('SOCKET_DATA')"><span class="reload-btn">소켓 데이터 Reload</span></el-button>
</section>


<section class="content-area">
  <div class="sk-area"> 
    <div  v-for="(item,index) in this.rt_sk_list" :key="index" :skInfo="item" :id="item.SK_ID" style="margin-bottom:20px">
      <skInToOut :key="index"  :skInfo="item" />
    </div>
  </div>
</section>

<div class="sk-msg-area">
      <h3>BZ/SCHEDULE</h3>
      <skMsgListTable />
</div>

</div>
</template>
<script>
import {targetComputed, targetMethod} from './store/testApiHelper'
import {rootComputed, rootMethod} from '../../store/rootStore/rootHelper'
/* eslint-disable no-mixed-spaces-and-tabs */
import skInToOut from './components/skInToOut.vue'
import skMsgListTable from './components/skMsgListTable.vue'
// import skHdArea from './components/skHdArea.vue'

export default {
  data () {
    return {
      dialogVisible:false,
      socket:null,
      skInfo:[],
      polling:null,
      content:`메시지, IN/OUT 정보만 리로드 합니다`
    }
  },
  components:{
    skInToOut,
    skMsgListTable
  },
  computed: {
    ...targetComputed,
    ...rootComputed
    
  },
  watch:{
     
  },
  methods: {
    ...targetMethod,
    ...rootMethod,

    callFunction (obj) {   

			if(this.polling !== null ){ clearInterval(this.polling) }

			this.polling = setInterval(()=>{
			   this.AcWKData(obj)
			   this.AcDvData(obj)
			},3000)

    },
    refrashInitData(flag){
      let sendForm = {
          COMMAND:'SYSTEM_MSG',
          FLAG: 'INIT_RELOAD',  // IN_SEND_TEST, OUT_SEND_TEST
          DATA_LIST:flag
        }
      this.rt_webSk.send(JSON.stringify(sendForm))

    }

  },
  created () {

  },
  mounted() {
   
  },
   beforeDestroy() {
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
    }
  },
}
</script>

<style>
.content-area{
  width: 100%;
  display: flex; 
  justify-content: space-between; 
}
.header-area{
  display: flex;
}
.sk-area{
  /* display: flex; */
  width:100%
}
.default_css{
  min-height: 200px;
}


.sk-msg-area{
  width: 100%;
    margin: 7px 0 6px 11px;
    padding: 12px;
    background: #F2F6FC;
    border-radius: 5px;
    border: solid 1px;
    border-color: #3d3939;
}
.box-card{
  min-height: 200px;
}
.title_h{
  text-align: left;
  font-size: 1em;
  margin-top: 0.2em;
  margin-bottom: 0;
}
.left_div{
  padding: 5px;
  /* border: solid 1px;
  border-color: rgb(84, 92, 100);
  border-radius: 8px; */
}
.right_div{
  height: 100%;
  padding: 5px;
  /* border: solid 1px;
  border-color: rgb(84, 92, 100);
  border-radius: 8px; */
}
.text .item{
  overflow: scroll;
}

.reload-btn{
  font-size: 14px;
}

</style>