import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
import inmemoryStore from './inmemoryStore'

export const targetComputed ={
    ...mapState('inmemoryStore',Object.keys(inmemoryStore.state)),
    ...mapGetters('inmemoryStore',Object.keys(inmemoryStore.getters))
}
export const targetMethod ={
    ...mapActions('inmemoryStore',Object.keys(inmemoryStore.actions)),
    ...mapMutations('inmemoryStore',Object.keys(inmemoryStore.mutations))
}