import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
import testApiStore from './testApiStore'

export const targetComputed ={
    ...mapState('testApiStore',Object.keys(testApiStore.state)),
    ...mapGetters('testApiStore',Object.keys(testApiStore.getters))
}
export const targetMethod ={
    ...mapActions('testApiStore',Object.keys(testApiStore.actions)),
    ...mapMutations('testApiStore',Object.keys(testApiStore.mutations))
}