<template>
  <div id="app">
    <el-container :style="'height:100%'">
      <el-header :style="'height:5%'"><mainHeader /></el-header>
      <el-main :style="'height:95%'"><router-view/></el-main>
    </el-container>
  </div>
</template>

<script>
import mainHeader from './components/Header.vue'
// import leftSide from './components/leftSide.vue'

import {rootComputed, rootMethod} from './store/rootStore/rootHelper'

export default {
  name: 'App',
  components: {
    mainHeader,
    // leftSide
  },
  computed:{
    ...rootComputed
  },
  methods:{
    ...rootMethod
  },
  mounted() {
    let webSk = null
    try {
      //webSk  = new WebSocket('ws://localhost:8400/system'); // 개발
      webSk  = new WebSocket(`ws://${location.host}/system`); // 운영
    } catch (error) {
      this.$message.error('error ::'+ error);
    }
    
    this.dufaltAssign({key:'rt_webSk', val:webSk})


    this.rt_webSk.onopen =() =>{
      //this.rt_webSk.send('{"COMMAND":"SYSTEM_MSG","FLAG":"INIT_INFO","DATA_LIST":""}')
    }
    this.rt_webSk.onerror =(msg) =>{
      this.$message.error('WebSocket error occurred ::'+ msg);
    }
    this.rt_webSk.onclose =(msg) =>{
      this.$message.error('WebSocket disconnected ');
    }

    this.rt_webSk.onmessage = ({data}) =>{
      let reviceData =JSON.parse(data)
      let command = reviceData.FLAG

      // console.log(reviceData)
    
      switch (command.trim()) {
          case 'INIT_INFO':

              //let temp  = JSON.parse(reviceData.DATA_LIST);
              let temp  = reviceData.DATA_LIST;
            
              
              // 소켓 리스트
              let skList = JSON.parse(temp.WEB_SK_LIST) // 해더정보까지 포함
              
              // in/out 메시지정보 반환
              //let msgList = JSON.parse(reviceData.MSG_INOUT_LIST)
              let inList = JSON.parse(temp.IN_LIST)
            
              let outList = JSON.parse(temp.OUT_LIST)

              let hdList = JSON.parse(temp.HD_LIST)
              let hdDtList = JSON.parse(temp.HD_DT_LIST)

              // body부 정보 반환
              // let bodyInfoList = JSON.parse(reviceData.MSG_BODY_LIST)
              let bodyList = JSON.parse(temp.BODY_LIST)  // 메시지 디테일 정보 포함
         
              
              let bodyDtList = JSON.parse(temp.BODY_DT_LIST)

              // init data
              let procList = JSON.parse(temp.PROC_LIST)
              let posList = JSON.parse(temp.POS_LIST)
              let lineList = JSON.parse(temp.LINE_LIST)

              let bzList = JSON.parse(temp.BZ_LIST)
             
              let schList = JSON.parse(temp.SCH_LIST)
            

              // 글로벌 stat에 저장
              this.dufaltAssign({key:'rt_in_list',val:inList})
              this.dufaltAssign({key:'rt_sk_list',val:skList})
              this.dufaltAssign({key:'rt_out_list',val:outList})
              this.dufaltAssign({key:'rt_hd_list',val:hdList})
              this.dufaltAssign({key:'rt_hd_dt_list',val:hdDtList})
              this.dufaltAssign({key:'rt_body_list',val:bodyList})
              this.dufaltAssign({key:'rt_body_dt_list',val:bodyDtList})

              this.dufaltAssign({key:'rt_bz_list',val:bzList})
              this.dufaltAssign({key:'rt_sch_list',val:schList})

              this.dufaltAssign({key:'rt_proc_list',val:procList})
              this.dufaltAssign({key:'rt_pos_list',val:posList})
              this.dufaltAssign({key:'rt_line_list',val:lineList})
              
              break;
          case 'OUT_MSG':
            
              let msgObj = reviceData.WEBSK_MSG
              let msg = msgObj.WEBSK_MSG

              let skId = msgObj?.SK_ID  // 소켓 아이디
              if(skId == null || skId == undefined){
                return;
              }
              let channel = msgObj.CHANNEL   // ip 정보
              let content = msgObj.BODY_DETAIL_LIST // 자바 MAP 형태의 문자열
              this.appendRtMsgList({
                  skId:skId,
                  channel: channel,
                  content: content
                })
              //this.dufaltAssign({key:'rt_body_dt_list',val:bodyDtList})
              break;
           case 'REFRASH_DATA':
            
              let temp2  = reviceData.DATA_LIST;
              for(let z in temp2){
               
                if('LINE_LIST' == z){
                    this.dufaltAssign({key:'rt_line_list',val:JSON.parse(temp2[z])})
                }else if("PROC_LIST" == z) {
                  this.dufaltAssign({key:'rt_proc_list',val:JSON.parse(temp2[z])})
                }else if("TOOL_LIST" == z) {
                  this.dufaltAssign({key:'rt_tool_list',val:JSON.parse(temp2[z])})
                }else if("POS_LIST" == z) {
                
                  this.dufaltAssign({key:'rt_pos_list',val:JSON.parse(temp2[z])})
                }
              }
              break;


        //응답 처리
          case 'SUCCESS':
               this.$message({
                message: 'Success',
                type: 'success'
              });
              break;
          case 'FAIL':
              this.$message({
                message: reviceData.DATA_LIST,
                type: 'Error'
              });
              break;
          default:
              break;
      }

      
    }
  },
  beforeDestroy() {
    // Clean up WebSocket connection when the component is destroyed
    if (this.rt_webSk) {
     
      this.rt_webSk.disconnect();
      this.dufaltAssign({key:'rt_webSk', val:null})
    }
  },
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');
body, html{
 height:  100%;
 background: #E4E7ED;
}

#app {
  font-family: 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
}

span{
  font-size: 1vw;
}
p {
font-size: 1vw;
}
h {
font-size: 1vw;
}
a {
  font-size: 1vw;
}

#app{height: 100%;}
html{min-height: 100%;}
</style>