<template>
    
  <div class="info-area">
  
    <!-- 소켓 정보 영역 -->
    <el-descriptions class="margin-top"  :column="7" :size="'medium'" border>
    <!-- :title="skInfo.SK_ID" -->
      <template slot="title">
        <p class="title_ct" :style="`background:${getBgColor}`">{{skInfo.SK_ID}}</p>
        <!-- <p :style="'margin: 0;    font-size: 5px;    color: gray;text-align: left;'">IN TO OUT</p> -->
      </template>

      <template slot="extra">
        <el-button type="primary" size="mini" @click="showMsg">IN/OUT INFO</el-button>
        <el-button type="primary" size="mini" @click="restart"><i class="el-icon-refresh"></i></el-button>
      </template>
      <el-descriptions-item label-class-name="sk-label">
        <template slot="label">
          <i class="el-icon-share"></i>
          socket ID
        </template>
        {{skInfo.SK_ID}}
      </el-descriptions-item>

      <el-descriptions-item label-class-name="sk-label">
        <template slot="label">
          <i class="el-icon-connection"></i>
          Connection type
        </template>
        {{skInfo.SK_CONN_TYPE}}
      </el-descriptions-item>

      <!-- <el-descriptions-item label-class-name="sk-label">
        <template slot="label">
          Socket group
        </template>
        {{skInfo.SK_GROUP == undefined ? '---' : skInfo.SK_GROUP}}
      </el-descriptions-item> -->

      <el-descriptions-item label-class-name="sk-label">
        <template slot="label">
          <i class="el-icon-location-outline"></i>
          Socket type
        </template>
        {{skInfo.SK_TYPE}}
      </el-descriptions-item>

      <el-descriptions-item label-class-name="sk-label">
        <template slot="label">
          <i class="el-icon-location-outline"></i>
          Header
        </template>
        {{skInfo.HD_ID}}
      </el-descriptions-item>
      
      <el-descriptions-item label-class-name="sk-label">
        <template slot="label">
          <i class="el-icon-office-building"></i>
          Address
        </template>
         {{`${skInfo.SK_IP}:${skInfo.SK_PORT}`}}
      </el-descriptions-item>

      <el-descriptions-item label-class-name="sk-label">
        <template slot="label">
          <i class="el-icon-office-building"></i>
          Delimit type
        </template>
         {{`${skInfo.SK_DELIMIT_TYPE}`}}
      </el-descriptions-item>
    </el-descriptions>

  

<div v-show="showBody">
 
<!-- 소켓 IN 영역 -->
    <div class="inout-area" >
      <div :style="'width:50%; border-right: solid;border-color: #9f9292;'">
        <el-table
        :empty-text="'NO DATA'"
        :size="'mini'"
        :class="'mycore-table'"
        :data="getInList"
        :header-row-class-name="setHeader"
        :header-cell-style="{ background: 'rgb(40 85 152)', color:'#EBEEF5' }"
        border
        style="width: 100%">

        <el-table-column
          prop="IN_MSG_ID"
          label="IN_MSG_ID"
          width="180">
        </el-table-column>
         <el-table-column
          prop="MSG_KEY_VAL"
          label="MSG_KEY_VAL"
          width="150"
          >
        </el-table-column>
         <el-table-column
          prop="IN_DESC"
          label="IN_DESC"
          width="120"
          >
        </el-table-column>

        <el-table-column
          prop="BZ_METHOD"
          label="BZ_METHOD"
          >
        </el-table-column>
         <el-table-column
          label=""
           width="70">
          <template slot-scope="scope">
            <!-- <el-button
              size="mini"
              @click="sendTest(scope.$index, scope.row)">send
              </el-button> -->
              <div class="bt-test" @click="msgTest(scope.row,'IN')"><i class="el-icon-right"></i></div>
          </template>
        </el-table-column>
      </el-table>
      </div>


<!-- 소켓 OUT 영역 -->
      <div :style="'width:50%'">
             <el-table
        :size="'mini'"
        :empty-text="'NO DATA'"
        :class="'mycore-table'"
        :data="getOutList"
        :header-row-class-name="setHeader"
        :header-cell-style="{ background: 'rgb(40 85 152)', color:'#EBEEF5' }"
        border
        style="width: 100%">


        <el-table-column
          prop="OUT_SK_ID"
          label="OUT_SK_ID"
          width="180">
        </el-table-column>
        
        <el-table-column
          prop="OUT_MSG_ID"
          label="OUT_MSG_ID"
          width="160">
        </el-table-column>
        

        <el-table-column
          prop="MSG_KEY_VAL"
          label="MSG_KEY_VAL"
          width="150"
          >
        </el-table-column>

        <el-table-column
          prop="OUT_DESC"
          label="OUT_DESC"
          >
        </el-table-column>

         <el-table-column
           label=""
           width="70"
           >
          <template slot-scope="scope">
              <div class="bt-test" @click="msgTest(scope.row,'OUT')"><i class="el-icon-right"></i></div>
          </template>
        </el-table-column>
      </el-table>
      </div>
    </div>


</div>


<!-- 메시지 팝업 영역 -->
    <el-dialog
      :title="`${msgId}  ::  ${msgKey}`"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
    
        <div>
       <el-table
        :size="'mini'"
        :empty-text="'NO DATA'"
        :class="'mycore-table'"
        :data="msgHdInfo"
        :header-row-class-name="setHeader"
        :header-cell-style="{ background: 'rgb(40 85 152)', color:'#EBEEF5' }"
        border
        style="width: 100%">

        <el-table-column
          prop="DT_ORD"
          label="DT_ORD"
          width="120">
        </el-table-column>
        <el-table-column
          prop="DT_ID"
          label="DT_ID"
          width="120">
        </el-table-column>
        

        
        <el-table-column
          prop="DT_LEN"
          label="DT_LEN"
          >
        </el-table-column>
        <el-table-column
          prop="DT_TYPE"
          label="DT_TYPE"
          >
        </el-table-column>
      </el-table>


      
      <el-table
        :size="'mini'"
        :empty-text="'NO DATA'"
        :class="'mycore-table'"
        :data="msgBodyInfo"
        show-summary
        :sum-text="'total len'"
        :header-row-class-name="setHeader"
        :header-cell-style="{ background: 'rgb(40 85 152)', color:'#EBEEF5' }"
        border
        style="width: 100%">

        <el-table-column
          prop="MSG_DT_ORD"
          label="MSG_DT_ORD"
          width="120">
        </el-table-column>
        <el-table-column
          prop="VAL_ID"
          label="VAL_ID"
          width="120">
        </el-table-column>
        
        <el-table-column
          prop="VAL_LEN"
          label="VAL_LEN"
          >
        </el-table-column>
        <el-table-column
          prop="VAL_TYPE"
          label="VAL_TYPE"
          >
        </el-table-column>

        <el-table-column
          label=""
          >
          <template slot-scope="scope">
              <div class="pData">
                  <input type="text" :maxlength="scope.row.VAL_LEN" class="inputs" :name="`${scope.row.VAL_ID}:${scope.row.VAL_TYPE}`" >
              </div>
          </template>
        </el-table-column>
        
      </el-table>

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">close</el-button>
        <el-button type="primary" @click="sendMsgTest">send test</el-button>
      </span>
    </el-dialog>
    </div>
</template>
<script>
import {targetComputed, targetMethod} from '../store/testApiHelper'
import {rootComputed, rootMethod} from '../../../store/rootStore/rootHelper'


export default {
     data () {
        return {
          size: '',
          centerDialogVisible:false,
          msgId:'',
          msgHdInfo:[],
          msgBodyInfo:[],
          msgKey:'',
          msgSkId:'',
          msgIdForSend:'',
          inoutType:'',
          bzMethod:'',
          showBody:false
        }
      },
      props:{
        skInfo: {
            type:Object,
            default: {}
        },
    },

    computed:{
      ...targetComputed,
      ...rootComputed,

      getBgColor(){
        let result = 'rgb(62 169 150)'
        if(this.skInfo.HD_ID == 'HD_FREE'){
          result = '#7e818a'
        }
        return result
      },

      getInList(){
            let skId = this.skInfo.SK_ID
            let skGroup = this.skInfo.SK_GROUP
            let InList = this.rt_in_list
            let temp = []
            for(let i in InList){
              if(InList[i].IN_SK_ID == skId || InList[i].IN_SK_ID == skGroup){  
              // if(InList[i].IN_SK_ID == skId && InList[i].IN_MSG_ID != undefined){
                  temp.push(InList[i])
              }
            }

          
            return temp
        },
         getOutList(){
            let skId = this.skInfo.SK_ID
            let skGroup = this.skInfo.SK_GROUP
            let outList = this.rt_out_list
            let temp = []

            for(let i in outList){
              if(outList[i].OUT_SK_ID == skId || outList[i].OUT_SK_ID == skGroup){
                  temp.push(outList[i])
              }
            }
           
            return temp
        }
    },
    methods:{
      ...targetMethod,
      ...rootMethod,


      showMsg(){
        // this.$refs.test.show()
        // console.log(this.$refs.test)
        // this.$refs.test.style= 'display:block'
        this.showBody = !this.showBody
      },


      sendMsgTest(){
        
        let inputs = document.getElementsByClassName('inputs')

        // 각 input 요소의 값을 가져와서 출력
        // 각 input 요소의 값을 가져와서 출력
        let msgInfo = {
            SK_ID:this.msgSkId,
            MSG_ID:this.msgIdForSend,
            BODY:[],
            BZ_METHOD:this.bzMethod
        }
        for (var i = 0; i < inputs.length; i++) {
          let value = inputs[i].value;
          let nameType = inputs[i].name.split(':');
          let name = nameType[0]
          let type = nameType[1]
          msgInfo.BODY.push({key:name, value:value, type:type})
        }
       
        let sendForm = {
          COMMAND:'SYSTEM_MSG',
          FLAG: this.inoutType+'_SEND_TEST',  // IN_SEND_TEST, OUT_SEND_TEST
          DATA_LIST:JSON.stringify(msgInfo)
        }
      
        this.rt_webSk.send(JSON.stringify(sendForm))
      },

      setHeader({row, rowIndex}){
        return 'table_hd'
      },

      test(){
        alert('')
        this.$refs.test.toggle("active")
      },

      msgTest(row,flag){
       
        let skId = row[flag+'_SK_ID']
        let msgId = row[flag+'_MSG_ID']

       

        // input 데이터 초기화
        let inputs = document.getElementsByClassName('inputs')
        for (var i = 0; i < inputs.length; i++) {
          inputs[i].value = '';
        }
        
        let socket
        for(let it in this.rt_sk_list){
            if(this.rt_sk_list[it].SK_ID == skId || this.rt_sk_list[it].SK_GROUP ==skId){
              socket = this.rt_sk_list[it]
            }
        }

        let hdId = socket.HD_ID
        let hdDtList = socket[hdId]

        let message
        for(let im in this.rt_body_list){
            if(this.rt_body_list[im].MSG_ID == msgId){
              message = this.rt_body_list[im]
            }
        }
       
        let msgBody = message[msgId]
        

        
        //let hdDtList = this.rt_hd_dt_list
        //let msgBody = this.rt_body_dt_list
        let msgKeyVal = row.MSG_KEY_VAL
        let header = this.skInfo.HD_ID
        let bzMethod = row?.BZ_METHOD
        
        this.msgId = msgId
        this.msgSkId = skId   // 소켓 아이디
        this.msgIdForSend = msgId   // 메시지 아이디
        this.msgKey = msgKeyVal  // MID 
        this.msgHdInfo = hdDtList  // header
        this.msgBodyInfo = msgBody // body
        this.inoutType=  flag
        this.bzMethod = bzMethod
        this.centerDialogVisible = true
      },
      
      restart(){
        if (confirm(this.skInfo.SK_ID+" :: 재시작 하시겠습니까?") == true) {
          
          let msg = {
            COMMAND:'SYSTEM_MSG',
            FLAG:'RESTART',
            DATA_LIST:this.skInfo.SK_ID
          }
          this.rt_webSk.send(JSON.stringify(msg))
        } 
      }
    }
  }
  </script>
  <style>

  .info-area{
    margin: 7px 0 6px 0;
    padding: 12px;
    background: #F2F6FC;
    border-radius: 5px;
    border: solid 1px;
    border-color: #9c9797;
  }

  .title_ct{
    margin: 0;
    
    padding: 3px 10px 3px 10px;
    border-radius: 4px;
    color: white;
  }

.inputs{
  width:100%;
}
.table_hd{
  color:black
}
.inout-area{
    display: flex;
    justify-content: flex-start;
}
.bt-test{
  text-align: center;
  background: #9bb9d8;
  color: white;
  width: 47px;
  border-radius: 3px;
}

table td {
  color: black !important;
  border-color:#bcb0b0 !important
}

.bt-test:hover{
  background: #327dc9;
  cursor: pointer;
}

.sk-label{
  background:rgb(93 111 137) !important;
  color:white !important
}
  </style>