import Vue from 'vue'
import Vuex from 'vuex'
import dbStore from '../store/dbStore/dbStore'
import rootStore from './rootStore/rootStore'
import comboStore from './comboStore/comboStore'

import testApiStore from '../views/testApi/store/testApiStore'
import inmemoryStore from '../views/Inmemory/store/inmemoryStore'


Vue.use(Vuex)

export const store = new Vuex.Store({
  modules: {
    dbStore: dbStore,
    rootStore:rootStore,
    testApiStore:testApiStore,
    comboStore:comboStore,
    inmemoryStore:inmemoryStore
    
  }
})