import Vue from 'vue'
import Router from 'vue-router'
// import MainPage from '../views/MainPage'

import testApi from '../views/testApi/testApi.vue'
import Inmemory from '../views/Inmemory/Inmemory.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base : '/',
  routes: [
    {
       path: '/',
       name: 'main',
       component: testApi
    },
    {
      path: '/main',
      name: 'main',
      component: testApi
    },
    {
      path: '/Inmemory',
      name: 'Inmemory',
      component: Inmemory
    }
  ]


})